










































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {listModule} from "../../../store/moudles/list/listing"
@Component
export default class ListMain extends Vue {
    @Prop() private msg: string;
    private name: string = "";
    private search: String;
    private total1: number = 0;
    privatecurrentPage: number = 1;
    privatepageSize: number = 10;
    private tempList: Array<Object> = [];
    private tableData: Array<Object> = [];


    get getBondsAllList() {
        return listModule.getTableData;
    }

    /**
     *  
     * @param index
     * @param row
     */
    private handleEdit(index, row) {
        const h = this.$createElement;
        this.$msgbox({
            title: '',
            message:h('div',null,[

            ]),
           /* message: h('p', null, [
                h('span', null, ' '),
                h('i', {style: 'color: teal'}, 'VNode')
            ]),*/
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            roundButton:true,
            beforeClose: (action, instance, done) => {
                if (action === 'confirm') {
                    instance.confirmButtonLoading = true;
                    instance.confirmButtonText = '...';
                    setTimeout(() => {
                        done();
                        setTimeout(() => {
                            instance.confirmButtonLoading = false;
                        }, 300);
                    }, 3000);
                } else {
                    done();
                }
            }
        }).then(action => {
            this.$message({
                type: 'info',
                message: 'action: ' + action
            });
        });
    };

    /**
     * 
     * @param index
     * @param row
     */
    private handleDelete(index, row) {
        let _this = this;
        console.log(index, row);
        _this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            roundButton:true,
            type: 'warning'
        }).then(() => {
            //
            listModule.changeTableData(index);
            _this.$message({
                type: 'success',
                message: '!'
            });
        }).catch(() => {
            _this.$message({
                type: 'info',
                message: ''
            });
        });
    };

    private handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.handleCurrentChange(this.currentPage);
    };
    private handleCurrentChange(currentPage) {//
        this.currentPage= currentPage;
        this.currentChangePage(listModule.getTableData, currentPage)
    };

    //
    private currentChangePage(list, currentPage) {
        let from = (currentPage - 1) * this.pageSize;
        let to = currentPage * this.pageSize;
        this.tempList = [];
        for (; from < to; from++) {
            if (list[from]) {
                this.tempList.push(list[from]);
            }
        }
    };

    created() {
        debugger;
        this.search = "";
        this.tempList = listModule.getTableData;
        this.tableData=listModule.getTableData;
    };

    mounted() {
    };

    destored() {

    }
}
